import NgxCommMsgHelper from '../NgxCommMsgHelper';
import PubSub from '../PubSub';

/**
 * PPService contains Point Play related work-flows between shell services, apps, content, and
 * nexgen.
 * @param this.ngxComm {object} Comm Interface service instance
 * @param this.logger {object} this.logger service instance
 * @constructor
 */
export default class PointPlayService {
    constructor(ngxComm, logger, appStore) {
        this.ngxComm = ngxComm;
        this.logger = logger;
        this.appStore = appStore;
        this.appStoreCache = null;

        // Constants
        // PP service publishes on this to respond to check PP enabled request
        this.CHECK_PP_ENABLED_RESP_TOPIC = 'enablePointPlayResp';
        this.INITIATE_PP_INIT_TOPIC = 'initPointPlay';
        this.INITIATE_PP_INIT_RESP_TOPIC = 'initPointPlayResp';
        // Subscribed to by PP service to convert points to either Credit or XC.
        this.CONVERT_PP_TOPIC = 'convertPointPlay';
        // PP service publishes on this to respond to convert points request
        this.CONVERT_PP_RESP_TOPIC = 'convertPointPlayResp';
        this.CMD_VERS = '1.0';

        this.enumInitiatePPStatus = {
            PP_AVAILABLE: 0,
            PP_NOT_AVAILABLE_EGM: 1,
            PP_NOT_AVAILABLE_PINLOCK: 2,
            PP_NOT_AVAILABLE_GOTO_BOOTH: 3,
            PP_NOT_AVAILABLE_BALANCE_LOW: 4,
            PP_NOT_AVAILABLE_CANNOT_CONVERT_XTRACREDIT: 5,
            PP_NOT_AVAILABLE_CANNOT_CONVERT_XTRACREDIT_EGM: 6,
            PP_NOT_AVAILABLE_PIN_FAILED: 7,
            PP_NOT_AVAILABLE: 8
        };

        this.enumPINStatus = {
            VALID: 1,
            INVALID: 2,
            LOCKED: 3,
            NO_RESPONSE: 4,
            NOT_ENTERED: 5
        };

        this.convertPointsToCredit = false;
        this.isAppPinRequired = false; //Content requested for Pin Validation

        // Private vars
        this.VALIDATE_PIN_TOPIC = 'validatePIN';
        this.VALIDATE_PIN_RESP_TOPIC = 'validatePINResp';
        this.ENABLE_PP_TOPIC = 'enablePointPlay';

        // Properties
        // Keeping track of subscribers for clean up. This is needed when each unit test creates a new
        // instance of PPService
        this.checkPPEnabledRespSubscribers = [];
        this.convertPPReqSubscribers = [];
        this.convertPPRespSubscribers = [];
        this.initiatePPReqSubscribers = [];
        this.initiatePPRespSubscribers = [];

        // Subscribe to amplify topics to receive relevant messages
        let convertPPFn = this.convertPP.bind(this);
        this.convertPPReqSubscribers.push(convertPPFn);
        PubSub.subscribe(this.CONVERT_PP_TOPIC, convertPPFn);

        let initiatePPFn = this.initiatePP.bind(this);
        this.initiatePPReqSubscribers.push(initiatePPFn);
        PubSub.subscribe(this.INITIATE_PP_INIT_TOPIC, initiatePPFn);

        //Will be subscribed only if PP determines pin is required.
        this.handleValidatePINRespFn = this.handleValidatePINResp.bind(this);

        // Subscribe to enablePointPlay that is sent from the content.
        PubSub.subscribe(this.ENABLE_PP_TOPIC, this.checkPPEnabled.bind(this));
    }

    checkPPEnabled() {
        this.processCheckPPEnabledResp();
    }

    processCheckPPEnabledResp() {
        const resp = {
            cmd: 'enablePointPlayResp',
            ver: '1.0',
            pointPlayEnabled: false
        };
        this.appStore.asyncGetAppStoreCache(()=>{
            return  this.appStore.AppStore.playerBonuses && this.appStore.AppStore.playerBonuses.pointPlay.enable !== null
        }).then((function (appStoreCache) {
            resp.pointPlayEnabled = appStoreCache.playerBonuses.pointPlay.enable;

            if (resp.pointPlayEnabled)
                this.logger.debug({ debug: 'PPService: Point Play is available.', data: resp }, true);
            else
                this.logger.debug({ debug: 'PPService: Point Play NOT available.', data: resp }, true);

            PubSub.publish(this.CHECK_PP_ENABLED_RESP_TOPIC, resp);
        }).bind(this));
    }

    /**
     * Helper function to create response message to client.
     * @returns Object {cmd: string, ver: string, playerPointBalance: number, status: number,amounts: Array}
     */
    createInitPPResponse() {
        return {
            cmd: this.INITIATE_PP_INIT_RESP_TOPIC,
            ver: this.CMD_VERS,
            playerPointBalance: 0,
            pointValue: 0,
            pointCredits: 0,
            status: 0
        };
    }

    /**
     * Helper function to convert PIN status into appropriate Point Play status
     * @param pinStatus: The status of PIN validation (2-5)
     * @returns number: The Point Play status corresponding to the PIN status (2, 7)
     */
    convertPinStatus(pinStatus) {
        // if pin locked
        return (pinStatus === this.enumPINStatus.LOCKED) ? this.enumInitiatePPStatus.PP_NOT_AVAILABLE_PINLOCK :
            this.enumInitiatePPStatus.PP_NOT_AVAILABLE_PIN_FAILED;
    }

    /**
     * Handle requested PIN validation response.
     * @param e validatePINResponse cmd
     */
    handleValidatePINResp(e) {
        const data = e.detail;
        this.logger.info({ info: 'PPService: PIN validation response received.', data: data }, true);
        //Dont unsusbcribe for invalid scenario, since it can become valid and can be entered later.
        if (data.status === this.enumPINStatus.VALID || data.status === this.enumPINStatus.LOCKED || data.status === this.enumPINStatus.NO_RESPONSE || data.status === this.enumPINStatus.NOT_ENTERED) {
            PubSub.unsubscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);
        }

        const resp = this.createInitPPResponse();
        if (data.status !== this.enumPINStatus.VALID) {
            // convert from PIN status to PP status.
            resp.status = this.convertPinStatus(data.status);

            // publish initPPResp object corresponding to pin validation.
            PubSub.publish(this.INITIATE_PP_INIT_RESP_TOPIC, resp);
        } else {
            this.sendBe2MsgToGetPlayerPoints();
        }

    }

    sendBe2MsgToGetPlayerPoints() {
        this.processInitiatePPResp();
    }

    processInitiatePPResp() {
        let minimumCents;
        let resp;

        this.appStore.asyncGetAppStoreCache(()=>{
            // return this.appStore.AppStore.playerBonuses.pointPlay.available_balance!=null
            return this.appStore.AppStore.playerBonuses
            && ((!this.appStore.AppStore.playerBonuses.xtraCredit.game_based && !this.appStore.AppStore.playerBonuses.xtraCredit.player_based
                || this.appStore.AppStore.playerBonuses.pointPlay.points_Limit_Credits != null || this.appStore.AppStore.playerBonuses.pointPlay.available_balance != null
                || this.appStore.AppStore.machine.denom != null)
            || (this.appStore.AppStore.playerBonuses.xtraCredit.game_based || this.appStore.AppStore.playerBonuses.xtraCredit.player_based
                && this.appStore.AppStore.playerBonuses.pointPlay.points_Limit_Credits != null && this.appStore.AppStore.playerBonuses.pointPlay.available_balance != null
                && this.appStore.AppStore.machine.denom != null))
        }).then((function (appStoreCache) {
            let playerPoints = appStoreCache.playerBonuses.pointPlay.available_balance;
            let pointsInCents = appStoreCache.playerBonuses.pointPlay.available_balance_in_Cents;
            let pointsCredit = appStoreCache.playerBonuses.pointPlay.available_balance_in_Credits;
            let pointsLimit = appStoreCache.playerBonuses.pointPlay.points_Limit;
            let pointsCreditLimit = appStoreCache.playerBonuses.pointPlay.points_Limit_Credits;
            let xtraCreditEnable = appStoreCache.playerBonuses.xtraCredit.game_based || appStoreCache.playerBonuses.xtraCredit.player_based;
            let egmDenom = appStoreCache.machine.denom;

            resp = this.createInitPPResponse();

            if (!this.convertPointsToCredit && !xtraCreditEnable) {
                resp.status = this.enumInitiatePPStatus.PP_NOT_AVAILABLE_CANNOT_CONVERT_XTRACREDIT_EGM;
                PubSub.publish(this.INITIATE_PP_INIT_RESP_TOPIC, resp);
                return;
            }

            // Check if points is over the limit.
            if (pointsLimit === 0 && pointsCreditLimit === 4000000000) {
                resp.status = this.enumInitiatePPStatus.PP_NOT_AVAILABLE_GOTO_BOOTH;
                PubSub.publish(this.INITIATE_PP_INIT_RESP_TOPIC, resp);
                return;
            }

            // If an egm denom is less than $2 (200) then we use the default minimum points balance.
            minimumCents = (egmDenom < this.appStore.minEgmDenom) ? this.appStore.getDefaultMinCentsBalance() :
                this.appStore.getMapEgmDenomToMinCentsBalance(egmDenom);

            if (pointsInCents < minimumCents) {
                resp.status = this.enumInitiatePPStatus.PP_NOT_AVAILABLE_BALANCE_LOW;
                PubSub.publish(this.INITIATE_PP_INIT_RESP_TOPIC, resp);
                return;
            }

            //send denom so patron can use it to adjust custom pointplay tables
            resp.egmDenom = egmDenom;
            resp.playerPointBalance = playerPoints;
            resp.pointValue = pointsInCents;
            resp.pointCredits = pointsCredit;
            resp.status = this.enumInitiatePPStatus.PP_AVAILABLE;

            PubSub.publish(this.INITIATE_PP_INIT_RESP_TOPIC, resp);
        }).bind(this));
    }

    /**
     * Function handler to initiate Point Play flow.
     * @param e Request data
     */
    initiatePP(e) {
        const data = e.detail;
        this.convertPointsToCredit = data.convertPointsToCredits;
        this.isAppPinRequired = data.validatePin;
        this.processCheckExecuteResp();
    }


    processCheckExecuteResp() {
        let isAbandonedCard;
        let isDuplicateCard;
        let isPPAvailable;
        let resp;

        this.appStore.asyncGetAppStoreCache(()=>{
            return  this.appStore.AppStore.playerBonuses 
            &&((this.appStore.AppStore.player_info.isAbandoned || this.appStore.AppStore.player_info.dupCard 
                || this.appStore.AppStore.playerBonuses.pointPlay.enable == false || this.appStore.AppStore.pin.locked 
                || this.appStore.AppStore.pin.force_pin || this.appStore.AppStore.pin.check_pin_needed)
            || (!this.appStore.AppStore.player_info.isAbandoned && !this.appStore.AppStore.player_info.dupCard 
                && this.appStore.AppStore.playerBonuses.pointPlay.enable && !this.appStore.AppStore.pin.locked 
                && !this.appStore.AppStore.pin.force_pin && !this.appStore.AppStore.pin.check_pin_needed))
        }).then((function (appStoreCache) {
            isPPAvailable = !!(appStoreCache.playerBonuses.pointPlay.enable);
            isAbandonedCard = !!(appStoreCache.player_info.isAbandoned);
            isDuplicateCard = !!(appStoreCache.player_info.dupCard);

            resp = this.createInitPPResponse();

            if (isAbandonedCard || isDuplicateCard) {
                this.logger.debug(
                    { debug: 'PPService: Card is a duplicate or the card is abandoned.', data: '' },
                    true
                );

                resp.status = this.enumInitiatePPStatus.PP_NOT_AVAILABLE_GOTO_BOOTH;
                PubSub.publish(this.INITIATE_PP_INIT_RESP_TOPIC, resp);
                return;
            }

            if (!isPPAvailable) {
                resp.status = this.enumInitiatePPStatus.PP_NOT_AVAILABLE;
                this.logger.debug({ debug: 'PPService: PP is NOT available.', data: resp }, true);

                PubSub.publish(this.INITIATE_PP_INIT_RESP_TOPIC, resp);
                return;
            }

            if (appStoreCache.pin.locked) {
                const be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
                    [NgxCommMsgHelper.enumBE2BasicFunc.PIN_NotEntered, [0, 0, 0, 0]]
                ]);
                this.ngxComm.sendBe2Message(be2FunctArray, this.doNothing.bind(this));

                resp.status = this.enumInitiatePPStatus.PP_NOT_AVAILABLE_PINLOCK;
                PubSub.publish(this.INITIATE_PP_INIT_RESP_TOPIC, resp);
                return;
            }

            //TODO: this.isAppPinRequired (handle M5 requires pin flag later)
            if (appStoreCache.pin.force_pin || appStoreCache.pin.check_pin_needed) {
                this.logger.info({ info: 'PPService: PIN required. Validating PIN.', data: null }, true);
                //lets clear all previous subscription for pin validation and start new here
                PubSub.unsubscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);
                PubSub.subscribe(this.VALIDATE_PIN_RESP_TOPIC, this.handleValidatePINRespFn);

                const validatePinReq = { cmd: 'validatePIN', ver: this.CMD_VERS };
                PubSub.publish(this.VALIDATE_PIN_TOPIC, validatePinReq);
                return;
            }

            this.sendBe2MsgToGetPlayerPoints();
        }).bind(this));
    }
    /**
     * Function to do nothing on BE2 responses of no interest.
     */
    doNothing(err, data) {
        if (err) {
            this.logger.error({ error: 'PPService: Received err in doNothing', data: err }, true);
            return;
        }
    }

    /**
     * This function processes requests to convert points.
     * @param e Request containing parameters convertPointsToCredits which gives the type of point
     * conversion to Credit or Xtra Credit and pointsToConvert which is the amount of points to
     * convert.
     */
    convertPP(e) {
        const data = e.detail;
        const resp = {
            cmd: 'convertPointPlayResp',
            ver: '1.0',
            status: 1
        };

        if (data.convertPointsToCredits === undefined || data.pointsToConvert === undefined) {
            this.logger.error({ error: 'PPService: Received Convert Points request with invalid parameters.', data: data }, true);
            PubSub.publish(this.CONVERT_PP_RESP_TOPIC, resp);
        } else if (data.pointsToConvert <= 0) {
            this.logger.error({ error: 'PPService: Received Convert Points request with invalid pointsToConvert parameter.', data: data }, true);
            PubSub.publish(this.CONVERT_PP_RESP_TOPIC, resp);
        } else {
            let code;
            // Points to Credits
            if (data.convertPointsToCredits) {
                this.logger.debug({ debug: 'PPService: Sending BE2 Basic Function request MP_PointsToCash.', data: '' }, true);
                code = NgxCommMsgHelper.enumBE2BasicFunc.MP_PointsToCash;
            } else {
                // Points to Xtra Credits
                this.logger.debug({ debug: 'PPService: Sending BE2 Basic Function request ' + 'MP_PointsToXtraCredits.', data: '' }, true);
                code = NgxCommMsgHelper.enumBE2BasicFunc.MP_PointsToXtraCredits;
            }

            const be2FunctArray = NgxCommMsgHelper.createBE2BasicFunctionArr([
                [code, [data.pointsToConvert, 0, 0, 0]]
            ]);
            this.ngxComm.sendBe2Message(be2FunctArray, this.processConvertPPResp.bind(this));
        }
    }

    /**
     * This function processes the point conversion response.
     * @param err Conversion error response.
     * @param data The response data.
     */
    processConvertPPResp(err, data) {
        const resp = {
            cmd: 'convertPointPlayResp',
            ver: '1.0',
            status: 2
        };

        if (err) {
            this.logger.error(
                { error: 'PPService: Received an error in processConvertPPResp.', data: err },
                true
            );
        } else {
            const be2BasicResp = data[NgxCommMsgHelper.BE2_BASIC_RESPONSE];
            if (
                be2BasicResp.length !== 1 ||
                (be2BasicResp[0].functionCode !== 26 && be2BasicResp[0].functionCode !== 70)
            ) {
                this.logger.error(
                    {
                        error: 'PPService: Received a BE2 response with invalid function code(s).',
                        data: data
                    },
                    true
                );
            } else {
                this.logger.debug({ debug: 'PPService: Points converted.', data: data }, true);
                resp.status = 0;
            }
        }

        PubSub.publish(this.CONVERT_PP_RESP_TOPIC, resp);
    }

    // Public Functions
    /**
     * Function to register Enable PP response callback.
     * The callback will be invoked when PPService publishes on CHECK_PP_ENABLED_RESP_TOPIC.
     *
     * @param callback function to be called when PP enabled check result is available.
     **/
    registerEnablePointPlayRespCB(callback) {
        this.checkPPEnabledRespSubscribers.push(callback);
        PubSub.subscribe(this.CHECK_PP_ENABLED_RESP_TOPIC, callback);
    }

    /**
     * Function to register to "initPointPlayResp" message response callback.
     * @param callback function to be called with result of Initiate PP
     */
    registerInitiatePointPlayRespCB(callback) {
        this.initiatePPRespSubscribers.push(callback);
        PubSub.subscribe(this.INITIATE_PP_INIT_RESP_TOPIC, callback);
    }

    /**
     * Function to register convert points response callback.
     * The callback will be invoked when PPService publishes on CONVERT_PP_RESP_TOPIC.
     *
     * @param callback function to be called when convert points result is available.
     **/
    registerConvertPPRespCB(callback) {
        this.convertPPRespSubscribers.push(callback);
        PubSub.subscribe(this.CONVERT_PP_RESP_TOPIC, callback);
    }

    /**
     * Cleanup subscriptions. Typically should be called in Unit tests before PPService instance
     * is destroyed.
     */
    cleanup() {
        // Un-subscribe all relevant amplify subscriptions
        if (this.checkPPEnabledRespSubscribers) {
            this.checkPPEnabledRespSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.CHECK_PP_ENABLED_RESP_TOPIC, item);
            });
        }
        if (this.initiatePPReqSubscribers) {
            this.initiatePPReqSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.INITIATE_PP_INIT_TOPIC, item);
            });
        }
        if (this.initiatePPRespSubscribers) {
            this.initiatePPRespSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.INITIATE_PP_INIT_RESP_TOPIC, item);
            });
        }
        if (this.convertPPReqSubscribers) {
            this.convertPPReqSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.CONVERT_PP_TOPIC, item);
            });
        }
        if (this.convertPPRespSubscribers) {
            this.convertPPRespSubscribers.forEach((item) => {
                PubSub.unsubscribe(this.CONVERT_PP_RESP_TOPIC, item);
            });
        }

        this.logger.debug({ debug: 'PPService: Cleaning up.' }, true);
    };
}