import {ref, watch, isRef} from "vue"
/**
 * Util is a static class filled with helper functions.
 */
export default class Util {
    /**
     * Sends a message to the targeted window using the post message api.
     *
     * @param targetWindow The window object the message will be sent to.
     * @param message The message to be sent.
     **/
    static postMessage(targetWindow, message) {
        try{
            targetWindow.postMessage( message, '*');
        }catch(e){
            targetWindow.postMessage( JSON.stringify(message, function replacer(key, val) {
                if (replacer.seen == undefined) {
                    replacer.seen = [];
                }
                if (val != null && typeof val == "object") {
                    if (replacer.seen.indexOf(val) >= 0) {
                        return;
                    }
                    replacer.seen.push(val);
                }
                return val;
            }), '*');
        }
    };

    static async asyncForEach(array, callback) {
        for (let index = 0; index < array.length; index++) {
            await callback(array[index], index, array)
        }
    };

}

export function asyncCallWithPredicate(predicate, func, cancelCondition = null, onHitCancelCondition=null) {
    let uw, uwr, cancel;
    let isActive = true;
    let p = new Promise(function (resolve, reject) {
        cancel = function (msg="MANUAL_CANCELED") {
            if (uw) {
                uw();
            }
            if(uwr){
                uwr();
            }
            reject(msg);
            isActive = false;
        }
        if (predicate()) {
            resolve(func())
            isActive = false;
        } else {
            uw = watch(predicate, function (isReady) {
                if (isReady) {
                    uw()
                    if(uwr){
                        uwr();
                    }
                    resolve(func())
                    isActive = false;
                }
            })
        }
    });
    if(isActive){
        cancelCondition = !isNaN(parseInt(cancelCondition)) && delay(parseInt(cancelCondition)) || cancelCondition;
        if(typeof cancelCondition == "function" 
                ? cancelCondition()
                : isRef(cancelCondition)
                    ? cancelCondition.value
                    : cancelCondition){
            cancel("AUTO_CANCELD");
            onHitCancelCondition && onHitCancelCondition();
        }else{
            if(cancelCondition){
                uwr = watch(cancelCondition, function(value){
                    if(value){
                        uwr();
                        cancel("AUTO_CANCELD");
                        onHitCancelCondition && onHitCancelCondition();
                    }
                });

            }

        }
    };
    //   return p;
   // let rst = { then: p.then.bind(p), catch: p.catch.bind(p), finally: p.finally.bind(p), cancel: cancel }
    let rst = { then: p.then.bind(p), catch: p.catch.bind(p), cancel: cancel }
    Object.defineProperty(rst, "isActive", {
        get() { return isActive }
    })
    return rst;
}

function delay(duration){
    let r = ref(false)
    setTimeout(()=>r.value = true,duration)
    return r;
}